

.navbar {
  background-image: linear-gradient(#FFFFFF, #E7EFF9, #CFD6E6);
  height: 80px;
  display: flex;
  width:100%;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  z-index: 1;
  font-family: 'PT Sans', sans-serif;
}


.navbar-logo {
  background-color: transparent;
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  margin-top:5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: #4F585E;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.tamnaPozadina {
  background-color: #4F585E;
  border-radius: 4px;
  color:#CFD6E6;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.nav-links:hover {
  background-color: #4F585E;
  color:#CFD6E6;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #000;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}
@media (min-width: 961px) and (max-width: 1200px){
  .navbar-logo {
    width: 80%;
    height:auto;
  }

}

@media only screen and (min-width: 729px) and (max-width: 969px){
  .navbar-logo {
    width: 70%;
    top:0%;
    left:0%;
    height:auto;
  }
  .NavbarItems {
    
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #4F585E;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    color:#CFD6E6;
    text-align: center;
    padding: 10px;
    width: 100%;
    display: table;
  }

  .nav-links-prvi {
    margin-top: 10px;
    margin-bottom: 5px;
  } 

  .nav-links:hover {
    background-color: #2D363D;
    border-radius: 0;
  }


  .menu-icon {
    color: #000;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #000;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #4F585E;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #2D363D;
    transition: 250ms;
  }

  button {
    display: none;
  }

}

