.cards {
    padding: 1rem;
    padding-top: 1%;
    background: #FFFFFF;
    color: #2D363D;
    font-family: 'Raleway';
    line-height: 26pt;
  }
  h1{
    margin-left: 20px;
    font-weight: bold;
    margin-bottom: 50px;
    color: #E53737;
    font-family: 'PT Sans', sans-serif;
  }
  .crniNaslov{
    color: #E7EFF9;

  }

.crno{
    
    background: #2D363D;
    color: #E7EFF9;
    font-family: 'Raleway';
}
  
.sivo{
    background-image: linear-gradient(#5D646A, #7F888D, #8D969C);
    color:  #2D363D;
    font-family: 'Raleway';
}
.sivoBezPrijelaza{
    padding-top: 90px;
    background:#8D969C;
    color:  #2D363D;
    font-family: 'Raleway';
}
  .textPrije {
    margin-bottom: 1%;
    margin-top: 1%;
    font-size: 22px;   
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1220px;
    width: 95%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 1%;
  }

  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 15px;
    font-weight: 800;
    color: #fff;
    background-color: #E53737;
    box-sizing: border-box;
    border-radius: 5px;
  }
  h4{
    font-weight: bold;
    color: #E53737;

  }
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    font-size: 18px;
    line-height: 25px;
    padding-top: 3%;
    line-height: 20pt;
  }

  .manji_font{
    font-size: 16px;
    width:85%;
  }
  .strelica{
    padding-right: 6px;
  }
  @media only screen and (min-width: 1200px) {

    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .textPrije {
        margin-bottom: 1%;
        margin-top: 1%;
        font-size: 19px;   
      }
    .cards__item {
      margin-bottom: 2rem;
    }
    .centrirano {
        padding-left: 12%;
      }
  }