.dropdown-menu {
    background: red;
    width: 200px;
    position: absolute;
    top: 80px;
    list-style: none;
    text-align: start;
  }
  
  .dropdown-menu li {
    background: #4F585E;
    cursor: pointer;
  }
  
  .dropdown-menu li:hover {
    background: #E65C5C;
  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
  }
  
  @media screen and (max-width: 960px) {
    .dropdown-menu {
      background: red;
      width: 200px;
      position: absolute;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      text-align: center;
      top: 70px;
      list-style: none;
      text-align: center;
     
    }
    .dropdown-menu li {
      background: #2D363D;
      cursor: pointer;
    }
  }